body{font:100% Helvetica,sans-serif;background-color:#0e1111;color:#fff}.row{display:flex;height:100vh;margin-left:auto;margin-right:auto}.row-kiri{display:flex;margin-left:auto;margin-right:auto;height:fit-content}.column{flex:50%;padding-left:0px;height:fit-content}.right{background-color:#0e1111;padding:20px}.left{background-color:#fff;margin:3px;height:100vh;border-radius:9px;padding:20px}.content-hitam{margin-left:auto;margin-right:auto;width:70%;padding-top:30px}h1,p{margin-bottom:0px}p,span{color:#414A4C}.bar{width:80%}#task{color:#fff;width:100%;margin-top:30px;margin-bottom:30px;padding:10px;border-radius:10px;background-color:#414A4C;border-style:none}::placeholder{color:#fff;opacity:1}:-ms-input-placeholder{color:#fff}::-ms-input-placeholder{color:#fff}@media screen and (max-width: 940px){.column,.left{width:100%;height:auto}}*{box-sizing:border-box}.project{float:left;width:33%;padding:0 10px;margin-bottom:10px}.row-projects{margin:0 -5px}.row-projects:after{content:"";display:table;clear:both}@media screen and (max-width: 940px){.project{width:100%;display:block;margin-bottom:20px}}.card{box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);padding:16px;text-align:center}.image{position:relative;text-align:center;color:#fff;margin:0px;background-size:cover;width:fit-content;display:inline-flex}.image img{background-color:#fadbe0;width:60px;height:60px;border-radius:15%}.corner{position:absolute;top:-6px;right:-6px;height:15px;width:15px;background-color:#fff;border-radius:50%}.centered{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.header h1,.header h3{color:black}.header p,.header form{color:#aaaaaa}.gambar-profile img{width:20px;height:20px}.list{display:block;position:relative;padding-left:35px;margin-bottom:35px;cursor:pointer;font-size:12px;user-select:none;color:#aaaaaa}.list input{position:absolute;opacity:0;cursor:pointer;height:0;width:0}.checkmark{position:absolute;top:0;left:0;height:25px;width:25px;background-color:#fff;border-radius:50%;border:1px solid #aaaaaa}.list:hover input ~ .checkmark{background-color:#ccc}.list input:checked ~ .checkmark{background-color:#12af92;border:none}.checkmark:after{content:"";position:absolute;display:none}.list input:checked ~ .checkmark:after{display:block}.list .checkmark:after{left:9px;top:5px;width:5px;height:10px;border:solid #fff;border-width:0 3px 3px 0;-webkit-transform:rotate(45deg);-ms-transform:rotate(45deg);transform:rotate(45deg)}.status{color:#00008b;margin-left:10px;padding:3px 12px;border-radius:500px;background-color:#add8e6;background-size:300px 100px}#tombol{border:none;color:#fff;padding:8px;text-align:center;text-decoration:none;display:inline-block;font-size:25px;margin:2px 30px;margin-bottom:25px;cursor:pointer;border-radius:15px;width:45px;height:45px;background-color:#9400D3;bottom:0;right:0;position:fixed;box-shadow:5px 10px 8px #888}#tombol-nav{background-color:#414A4C;border:none;color:#fff;padding:8px;text-align:center;text-decoration:none;display:inline-block;font-size:25px;margin:2px 30px;margin-bottom:25px;cursor:pointer;border-radius:15px;width:45px;height:45px}
